  createUser = () => {
    const {email, password} = this.state

    this.props.createUser({variables: {email, password}})
      .then((response) => {
        window.location.href = "https://i.imgflip.com/1sevnt.jpg";
      }).catch((e) => {
        console.error(e)
        this.props.router.replace('/')
      })
  }
